import { PageDataDTO, RecipeDTO, NoteDTO, VariationDTO, IngredientDTO, StepDTO, Option, StepDetailDTO } from "../model/recipe";
const recipesMapper = (recipesObj) => {
    // Convert object to Map
    const recipesYaml = new Map(Object.entries(recipesObj));
    const recipesDTO = new Map();
    for (const [recipeId, recipeYaml] of [...recipesYaml]) {
        const recipeDTO = recipeMapper(recipeYaml);
        recipesDTO.set(recipeId, recipeDTO);
    }
    return recipesDTO;
};
const recipeMapper = (recipe) => {
    const ingredients = ingredientsMapper(recipe.ingredients);
    const steps = stepsMapper(recipe.steps);
    const notes = notesMapper(recipe.notes);
    const options = optionsMapper(recipe.options);
    return new RecipeDTO(ingredients, steps, notes, options, recipe.yieldType, recipe.preconditions, recipe.yield, recipe.pictures);
};
const stepsMapper = (steps) => {
    return steps.map(step => {
        const details = step.details.map(detail => {
            if (typeof detail === "string") {
                return detail;
            }
            else {
                return new StepDetailDTO(detail.label, detail.link);
            }
        });
        return new StepDTO(step.label, step.emoji, details);
    });
};
const ingredientsMapper = (ingredients) => {
    return ingredients.map(ingr => new IngredientDTO(ingr.label, ingr.qty, ingr.unit, ingr.variable, ingr.link));
};
const optionsMapper = (options) => {
    let allOptions = (options || "").split(",");
    return allOptions
        .map(option => option.trim())
        .map(option => {
        switch (option) {
            case Option.HYDRATION: return Option.HYDRATION;
            case Option.YEAST: return Option.YEAST;
            default: return null;
        }
        ;
    })
        .filter(option => option !== null);
};
const notesMapper = (notes) => {
    if (!notes) {
        return [];
    }
    return notes.map(note => {
        if (typeof note === "string") {
            return note;
        }
        else {
            return new NoteDTO(note.label, note.link);
        }
    });
};
const variationsMapper = (variations) => {
    if (!variations) {
        return [];
    }
    return variations.map(variation => {
        return new VariationDTO(variation.label, variation.todo);
    });
};
export var Mappers;
(function (Mappers) {
    function pageData(source) {
        const recipes = recipesMapper(source.recipes);
        const notes = notesMapper(source.notes);
        const variations = variationsMapper(source.variations);
        return new PageDataDTO(source.title, source.preconditions, recipes, notes, variations);
    }
    Mappers.pageData = pageData;
})(Mappers || (Mappers = {}));
